/* Project Styles */

:root {
  --white: hsl(0, 0%, 100%);
  --grey: hsl(0, 0%, 50%);
  --yellow: hsl(47, 88%, 63%);
  --black: hsl(0, 0%, 7%);
  --font-family: 'Figtree', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font-family);
}

body {
  height: 100vh;
  width: 100vw;
  background-color: var(--yellow);
  display: flex;
  justify-content: center;
  align-items: center;
}

main.main {
  height: 525px;
  width: 380px;
  background-color: var(--white);
  border-radius: 15px;
  border: 2px solid black;
  box-shadow: 8px 8px black;
  padding: 20px;
}

.blogimage{
  border-radius: 10px;
  width: 100%;
}
.learnbtn{
  margin-top: 20px;
  background-color: var(--yellow);
  outline: none;
  border: none;
  border-radius: 5px;
  width: 5rem;
  height: 30px;
  font-weight: 800;
  font-size: 15px;
}

.publish{
  margin-top: 16px;
  font-weight: 600;
  font-size: 15px;
}

.foundation{
  margin-top: 20px;
  font-weight: 800;
}

.desc{
  margin-top: 20px;
  color: var(--grey);
  font-weight: 500;
  line-height: 22px;
}

.profile{
  margin-top: 25px;
  display: flex;
  align-items: center;
}

.profileimage{
  width: 30px;
}

.profilename{
  font-weight: 800;
  margin-left: 10px;
}

main.main:has(.foundation:hover){
  box-shadow: 15px 15px black;
}

.foundation:hover{
  color: var(--yellow);
  cursor: pointer;
}

@media(max-width:576px){
  main.main {
      height: 485px;
      width: 320px;
  }
  .blogimage{
      border-radius: 10px;
      width: 100%;
  }
  .foundation{
      font-size: 22px;
  }
}